* {
  box-sizing: border-box;
  color: white;
  margin: 0;
  padding: 0;
}

.root {
  background-image: url('./images/bg.jpg');
  background-size: cover;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
}

.planet-0 {
  animation: 30s 5s rotate infinite alternate linear;
  background-color: #472c1b;
  border-left: 5px white solid;
  border-radius: 50%;
  height: 80px;
  left: 15%;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 80px;
}

