.donw-arrow {
  background: none;
  border: none;
  position: absolute;
  right: 30px;
  top: 10px;
  transition: 0.5s;
  width: 25px;
}

.donw-arrow img {
  background-color: white;
  border-radius: 100%;
  cursor: pointer;
  width: 25px;
}

.show-filter {
  height: 100%;
}

.rotate-button {
  transform: rotate(180deg);
  transition: 0.5s;
}

@keyframes rotate {

  0% {
    opacity: 0.4;
    transform: rotate(0) translate(0, 0);
  }

  100% {
    opacity: 0.4;
    transform: rotate(360deg) translate(1000%, 1000%);
  }
}
